@import '../../style/typos';
@import '../../style/color';

.navbar {
    width: 100vw;
    height: 40px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-around; 
    align-items: center;
    background: linear-gradient($navbar-fade-gradient, $navbar-fade-gradient 0%, transparent 100%);
    border-bottom: 2px solid $light-typo;
    z-index:1;

    &__handler {
        height: 100%;
        font-size: 1rem;
        font-family: $footer-font;
        display: flex; 
        justify-content: center;
        align-items: center;
        text-transform: capitalize;
        cursor: pointer;
        text-decoration: none;
        background-color: transparent;
        border: none;
        color: $navbar-link-color;

        &:hover {
            color: $landing-link;
        }

        &--focus {
            color: $landing-link;
        }
    }
}

@media (max-width: 700px) {

    .navbar * {
        font-size: 0.8rem;
    }
}